import {ChangeDetectionStrategy, Component, ElementRef, Input, ViewEncapsulation} from '@angular/core';
import {toBoolean} from '../common/utilities';
import {AdaptTranslateService} from '../common/i18n/index';
import {
  AdaptRadarAngularCustomEventName,
  AdaptRadarAngularGenericDirective,
  AdaptRadarAngularService
} from '../common/radar/adapt-radar';

@Component({
  selector: 'adapt-not-found-page',
  // eslint-disable-next-line @angular-eslint/no-host-metadata-property
  host: {
    '[class.not-found]': 'true'
  },
  templateUrl: './not-found.component.html',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  preserveWhitespaces: true
})

export class AdaptNotFoundPageComponent extends AdaptRadarAngularGenericDirective {

  /**
   * Style class of the 404 page
   * @docs-default ''
   */
  @Input() mainCls: string = '';

  /**
   * Configuration for the text on a page - title text
   * @docs-default 404
   */
  @Input() title: string;

  /**
   * Configuration for the text on a page - sub title text
   * @docs-default Oops... we couldn't find that page
   */
  @Input() subTitle: string;

  /**
   * Configuration for the text on a page - description text
   * @docs-default Make sure the URL is spelled correctly. If it is, try refreshing the page
   */
  @Input() description: string;

  /**
   * Configuration for the text on a page - button text
   * @docs-default Back to home
   */
  @Input() btnText: string;

  /**
   * Configuration for button link
   * @docs-default #/landing
   */
  @Input() btnLink: string = '#/landing';

  /**
   * Specifies the type of the button
   * @docs-default secondary
   */
  @Input() btnType: string = 'secondary';

  /**
   * Specifies the size of the button
   * @docs-default xtra-large
   */
  @Input() btnSize: string = 'xtra-large';

  /**
   * Routers params
   */
  /**
   * RouterLink definition for internal navigation
   * @docs-default
   */
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() routerLink: any;

  /**
   * Query parameters for internal navigation via routerLink
   * @docs-default
   */
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() queryParams: any;

  /**
   * Configuration for active router link
   * @docs-default
   */
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() routerLinkActiveOptions: any;

  /**
   * When set as true, disables the button
   * @docs-default false
   */
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() get disabled(): any {
    return this._disabled;
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  set disabled(v: any) {
    this._disabled = toBoolean(v);
  }

  private _disabled: boolean = false;

  constructor(private ts: AdaptTranslateService,
              _adaptRadarService: AdaptRadarAngularService,
              _elem: ElementRef<HTMLElement>) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptNotFoundPage);

    const translate = ts.getCurrentLanguage();

    this.title = translate['adapt.notFound.title'];
    this.subTitle = translate['adapt.notFound.subTitle'];
    this.description = translate['adapt.notFound.description'];
    this.btnText = translate['adapt.notFound.back'];
  }

}
